import region_list from '@/components/region/list'
import chaobject from "@/components/script/chaobject";
import cha_list from "@/components/cha/item/cha_list";

export default {
  name: "list",
  mixins: [chaobject],
  components: {
    region_list,
    cha_list
  },
  data() {
    return {
      cha_datas: undefined,
      start: 0,
      limit: this._g.PER_PAGE_COUNT,
      total_count: undefined,
      title: undefined,
      region: undefined,
      type: undefined,
      page: 1,
      page_num: undefined,
      target_region:{

      }
    }
  },
  created() {
    this.load_list();
  },
  methods: {
    load_list: function () {
      var r = this.region = this.$route.query.r;
      var p = this.$route.query.p;
      var type = this.type = this.$route.query.type;
      this.page = parseInt(this.$route.query.page);
      if (!this.page) {
        this.page = 1;
      }
      console.log('type:', type);

      var set_title = '';

      if (type == 2) {
        set_title += " " + "兼职/楼凤";
      } else if (type == 1) {
        set_title += " " + "洗浴按摩/会所";
      }

      if (r) {
        set_title = set_title + "  " + r;
      }

      this.title = set_title;
      this.set_title(this.title);

      this.update_start();
      this.get_list({t: type, p: p, r: r, start: this.start, limit: this.limit, cb: this.on_list});
    },
    on_list: function (response) {
      console.log('[on_list]response:', response);
      this.cha_datas = response.data.datas;
      this.total_count = response.data.total_count;

      this.target_region.province = response.data.province;
      this.target_region.city = response.data.city;

      var tatal_page_num = parseInt((this.total_count / this._g.PER_PAGE_COUNT));
      this.page_num = tatal_page_num;

      console.log('page_num:', this.page_num);
      this.$forceUpdate();
    },
    on_page_change: function (e) {
      let url = this.update_url_param({
        'page': this.page
      });
      console.log('on_page_change:', url);
      this.$router.push(url);
    },
    update_start: function () {
      this.start = (this.page - 1) * this.limit;
    }
  },
  watch: {
    '$route'(to, from) {
      this.cha_datas = undefined;
      this.$nextTick(this.load_list);
    }
  },
}
